import { React, useEffect } from "react";
import { dataContext } from "../App.js";
import { useContext } from "react";

import { useNavigate } from "react-router-dom";
import { useState } from "react";
import { wrapGrid } from "animate-css-grid";

export default function Projects() {
  const Data = useContext(dataContext);
  const [searchInput, setSearchInput] = useState("");
  const [projectName, setProjectName] = useState("");
  const grid = document.querySelector(".projectContainer");

  const animateOptions = {
    // int: default is 0 ms
    stagger: 100,
    // int: default is 250 ms
    duration: 900,
    // string: default is 'easeInOut'
    easing: "easeInOut",
    // function: called with list of elements about to animate
    /*onStart: (animatingElementList) => {},
    // function: called with list of elements that just finished animating
    // cancelled animations will not trigger onEnd
    onEnd: (animatingElementList) => {},*/
  };
  wrapGrid(grid, animateOptions);
  let result = [];

  if (projectName === undefined) {
    result = Data;
  } else {
    result = Data.filter(checkName);

    function checkName(data) {
      if (
        data.description.includes(projectName.toLowerCase()) ||
        data.name.includes(projectName)
      )
        return { ...data };
    }
  }
  const filteredresult = result.map((item, index) => {
    return (
      <div className="projectImage">
        <div className="imagedisplay">
          <img
            className="image"
            src={item.imageUrl}
            onClick={() => window.open(item.gitLive)}
          ></img>
          <h4>{item.name}</h4>
          <p>{item.description}</p>
          <button
            className="project-buttonnew"
            onClick={() => window.open(item.gitLive)}
          >
            Live Pages
          </button>
          <button
            className="project-buttonnew"
            onClick={() => window.open(item.gitUrl)}
          >
            Git Hub
          </button>
        </div>

        {/*<div className="project--mainContent">
          <h4>{item.name}</h4>

          <div className="aboutProject">
            <p>{item.description}</p>

            <button
              className="project-button"
              onClick={() => window.open(item.gitLive)}
            >
              Live Pages
            </button>
            <button
              className="about-button"
              onClick={() => window.open(item.gitUrl)}
            >
              Git Hub
            </button>
          </div>
        </div>*/}
      </div>
    );
  });
  console.log(filteredresult);

  function handleChange(e) {
    setSearchInput(e.target.value);
  }

  return (
    <div>
      <div className="projects">
        <div className="header--project">
          <h3>PROJECTS</h3>
          <p className="underline"></p>
          <p>Here you will find some of the projects that I created</p>
        </div>
        <div className="buttonClass">
          <button
            onClick={(e) => setProjectName(e.target.name)}
            name=""
            className="searchButtons"
          >
            All
          </button>
          <button
            onClick={(e) => setProjectName(e.target.name)}
            name="Html"
            className="searchButtons"
          >
            Html/Css
          </button>
          <button
            onClick={(e) => setProjectName(e.target.name)}
            name="Api"
            className="searchButtons"
          >
            Api
          </button>
          <button
            onClick={(e) => setProjectName(e.target.name)}
            name="Javascript"
            className="searchButtons"
          >
            JavaScript
          </button>
          <button
            onClick={(e) => setProjectName(e.target.name)}
            name="React"
            className="searchButtons"
          >
            React
          </button>
          <button
            onClick={(e) => setProjectName(e.target.name)}
            name="Mern"
            className="searchButtons"
          >
            Mern
          </button>
          <button
            onClick={(e) => setProjectName(e.target.name)}
            name="Game"
            className="searchButtons"
          >
            Games
          </button>
        </div>
        {/* <div
          style={{
            marginLeft: "75%",
            marginRight: "100px",
            display: "flex",
            columnGap: "10px",
            marginTop: "auto",
          }}
        >
          <input
            type="text"
            placeholder="Html,React,Javascript,Css,Api"
            onChange={handleChange}
          ></input>
          <button
            style={{
              width: "30px",
              height: "30px",
              backgroundImage: `url(${search})`,
              backgroundRepeat: "no-repeat",
              backgroundSize: "cover",
              border: "none",
            }}
            onClick={handleClick}
          ></button>
          </div>*/}
        <div className="projectContainer">{filteredresult}</div>
      </div>
    </div>
  );
}
