import React from "react";
import Background from "../images/background.jpg";
import Profile from "../images/profilepic.png";
import Git from "../images/git.png";
import LinkedIn from "../images/linkedin.png";
import { useNavigate } from "react-router-dom";

export default function Home() {
  const navigate = useNavigate();

  return (
    <div
      className="home"
      style={{
        backgroundImage: `url(${Background})`,
        backgroundRepeat: "no-repeat",
        backgroundSize: "cover",
      }}
    >
      <div className="socialMedia">
        <img
          src={Git}
          alt=""
          onClick={() =>
            window.open("https://github.com/Prescilla2022?tab=repositories")
          }
        />
        <img
          src={LinkedIn}
          alt=""
          onClick={() =>
            window.open("https://www.linkedin.com/in/prescillaanuop")
          }
        />
      </div>
      <div className="profile">
        <div
          className="profilepic"
          style={{
            backgroundImage: `url(${Profile})`,
            backgroundRepeat: "no-repeat",
            backgroundSize: "cover",
          }}
        ></div>
      </div>

      <div className="mainContent">
        <div>
          <h1>HEY, IM PRESCILLA</h1>
          <p>
            I'm an enthusiastic fullstack developer with a passion for clean
            design, testdriven development and perfectly ripe avocados.
          </p>
          <button
            className="home-button"
            onClick={() => navigate("/portfolio/Projects")}
          >
            PROJECTS
          </button>
        </div>
      </div>
    </div>
  );
}
