import React from "react";
import { dataContext } from "../App.js";
import { useContext } from "react";
import { useParams } from "react-router-dom";

export default function Search(props) {
  const Data = useContext(dataContext);

  const { projectName } = useParams();
  //const projectName = props.searchValue;
  let result = [];
  console.log(projectName);

  if (projectName === undefined) {
    result = Data;
    //console.log(result);
  } else {
    result = Data.filter(checkName);
    //console.log(result);

    function checkName(data) {
      if (
        data.description.includes(projectName.toLowerCase()) ||
        data.name.includes(projectName)
      )
        return { ...data };
    }
  }
  const filteredresult = result.map((item, index) => {
    return (
      <div className="container" key={index}>
        <div className="projectImage">
          <div className="imagedisplay">
            <img className="image" src={item.imageUrl}></img>
          </div>
        </div>
        <div className="project--mainContent">
          <h4>{item.name}</h4>

          <div className="aboutProject">
            <p>{item.description}</p>

            <button
              className="project-button"
              onClick={() => window.open(item.gitLive)}
            >
              Live Pages
            </button>
            <button
              className="about-button"
              onClick={() => window.open(item.gitUrl)}
            >
              Git Hub
            </button>
          </div>
        </div>
      </div>
    );
  });
  return (
    <div className="projects">
      Search results for "{projectName}"
      <div className="projectContainer">{filteredresult}</div>
    </div>
  );
}
