import React from "react";
import Link from "../images/Link.png";
import Mail from "../images/mail.png";
import Call from "../images/call.png";
import Background from "../images/background.jpg";
import { useState, useRef } from "react";
import emailjs from "emailjs-com";
import swal from "sweetalert";
import Modal from "../components/Modal";

export default function Contact() {
  const [from_name, setFullname] = useState("");
  const [from_email, setEmail] = useState("");
  const [message, setMessage] = useState("");
  const [open, setOpen] = useState(false);
  const date = new Date();
  const form = useRef();
  const SERVICE_ID = "service_ifdugxq";
  const TEMPLATE_ID = "template_xgoc73f";
  const PUBLIC_KEY = "M5zZwvUSy6N0I9iW0";

  //Function to handle when contact form is submitted
  function handleclick(e) {
    e.preventDefault();
    /*var data = {
      from_name: fullname,
      message: message,
      from_email: email,
    };*/
    if (from_email === "" || from_name === "" || message === "") {
      setOpen(true);
    } else {
      setOpen(false);
      (function () {
        emailjs.init("M5zZwvUSy6N0I9iW0");
      })();

      emailjs
        .sendForm(
          "service_ifdugxq",
          "template_xgoc73f",
          form.current,
          "M5zZwvUSy6N0I9iW0"
        )
        .then(
          (result) => {
            console.log(result.text);
          },
          (error) => {
            console.log(error.text);
          }
        );

      swal("Thanks for contacting!!!");
      window.location.reload();
    }
  }

  //functions to handle the change in input fields
  function handlename(event) {
    setFullname(event.target.value);
    console.log(from_name);
  }

  function handleemail(event) {
    setEmail(event.target.value);
    console.log(from_email);
  }
  function handlemessage(event) {
    setMessage(event.target.value);
    console.log(message);
  }

  return (
    <div
      style={{
        backgroundImage: `url(${Background})`,
        backgroundRepeat: "no-repeat",
        backgroundSize: "cover",
      }}
    >
      <div className="projects">
        <div className="header--project">
          <h3>CONTACT</h3>
          <p className="underline"></p>
          <p>Feel free to contact me through email or linkedIn</p>
        </div>

        <div className="contact">
          <form ref={form} className="form" onSubmit={handleclick}>
            <input
              type="text"
              onChange={handlename}
              name="from_name"
              placeholder="Name"
              required
            ></input>

            <input
              type="email"
              name="from_email"
              onChange={handleemail}
              placeholder="Email"
              required
            ></input>
            <textarea
              onChange={handlemessage}
              placeholder="Message"
              name="message"
              required
            ></textarea>

            <button
              type="submit"
              className="contact-button"
              onClick={handleclick}
            >
              CONTACT
            </button>
            {open && <Modal />}
          </form>

          <div
            style={{
              padding: `40px`,
              width: "50%",
            }}
          >
            <div className="social">
              <p className="imageStyle">
                <img src={Mail}></img>
              </p>
              <div className="content">
                <p>E-mail</p>
                <p>prescilla.kodehode@gmail.com</p>
              </div>
            </div>
            <div className="social">
              <p className="imageStyle">
                <img src={Link}></img>
              </p>
              <div className="content">
                <p>LinkedIn</p>
                <a href="https://www.linkedin.com/in/prescillaanuop">
                  www.linkedin.com/in/prescillaanuop
                </a>
              </div>
            </div>
            <div className="social">
              <p className="imageStyle">
                <img src={Call}></img>
              </p>
              <div className="content">
                <p>Call</p>
                <p>+47 93983554</p>
              </div>
            </div>

            <div></div>
          </div>
        </div>
      </div>
    </div>
  );
}
